@charset "utf-8"; @import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,600;1,100;1,200&family=Noto+Sans+KR:wght@100;400&display=swap');:root * { font-family: 'Montserrat', sans-serif; font-family: 'Noto Sans KR', sans-serif; letter-spacing: 0px; box-sizing: border-box;

}::-webkit-scrollbar { display: none; }
* { margin: 0px; padding: 0px; box-sizing: border-box; /* color: #000; */
}
body { font-size: 1rem; font-family: 'Montserrat', sans-serif; color: #000; background-color: #31333B;  min-width: 320px; }
.noto { font-family: 'Noto Sans KR' l, sans-serif; }
ol, ul { list-style: none; }
a { text-decoration: none; color: inherit; }
a:hover, a:active { text-decoration: none; color: #fff; }
img { vertical-align: bottom; }
em { flex-shrink: normal; }
button { border: none; background: transparent; }
.container { width: 1280px; margin: 0 auto; }
.bg { width: 70%; margin: 0 auto; overflow: hidden; margin-bottom: 50px; }
.bg img { width: 100%; object-fit: cover; }
.title { min-height: 70px; font-weight: 500; font-size: 45px; padding: 3% 0 0 0; }
.text { min-height: 10px; line-height: 30px; font-size: 18px; font-weight: 100; }
section { width: 100%; min-height: 100%; display: flex; align-items: center; }
.yellow { color: #FFC86B; }
/* 첫번재 섹션 */

.paddignFull { width: 1180px; margin: 0 auto; overflow: hidden; }
.intro-wrap .inner { display: flex; justify-content: center; align-items: center; font-size: 25px; letter-spacing: -0.8px; }
.intro-wrap .inner li:nth-child(1) { flex: 1; }
.intro-wrap .inner li:nth-child(1) p { margin-bottom: 20px; }
.intro-wrap li a { padding: 8px; border: 1px solid #FFC86B; border-radius: 4px; font-size: 18px; }
.intro-wrap li a:hover { background-color: #FFC86B; color: #fff; transition: 0.8s; }
/* 두번째 섹션 */
.about-wrap .skill-inner ul { display: grid; gap: 20px; grid-template-columns: repeat(4,1fr); margin-top: 20px; }
.about-wrap .skill-inner li { height: 200px; border: 1px solid white; border-radius: 8px; justify-content: center; align-items: center; display: flex; flex-direction: column; }
/* 세번째 섹션 */
.contents-wrap ul { display: grid; gap: 20px; grid-template-columns: repeat(3,1fr); margin-top: 20px; }
.item { height: 300px; font-family: 'Raleway', sans-serif; position: relative; display: inline-block; overflow: hidden; margin: 5px; color: #ffffff; font-size: 16px; text-align: center; overflow: hidden; }
.item * { -webkit-box-sizing: border-box; box-sizing: border-box; -webkit-transition: all 0.25s ease; transition: all 0.25s ease; }
.contents-wrap .item:before { position: absolute; top: 10px; bottom: 10px; left: 10px; right: 10px; top: 100%; content: ''; background-color: rgba(51, 51, 51, 0.9); -webkit-transition: all 0.25s ease; transition: all 0.25s ease; -webkit-transition-delay: 0.25s; transition-delay: 0.25s; }
.contents-wrap .item img { width: 100%; height: 100%; object-fit: cover; }
.contents-wrap .item span { position: absolute; top: 0; bottom: 0; left: 0; right: 0; z-index: 1; align-items: center; display: flex; flex-direction: column; justify-content: center; }
.contents-wrap .item h3, .item h5 { margin: 0; opacity: 0; letter-spacing: 1px; }
.contents-wrap .item h3 { -webkit-transform: translateY(-100%); transform: translateY(-100%); text-transform: uppercase; font-weight: 400; -webkit-transition-delay: 0.05s; transition-delay: 0.05s; margin-bottom: 5px; }
.contents-wrap .item h5 { font-weight: normal; background-color: #FFC86B; padding: 3px 10px; -webkit-transform: translateY(-100%); transform: translateY(-100%); -webkit-transition-delay: 0s; transition-delay: 0s; }
.contents-wrap .item:hover:before, .contents-wrap .item.hover:before { top: 10px; -webkit-transition-delay: 0s; transition-delay: 0s; }
.contents-wrap .item:hover h3, .contents-wrap .item.hover h3, .contents-wrap .item:hover h5, .contents-wrap .item.hover h5 { -webkit-transform: translateY(0); transform: translateY(0); opacity: 1; }
.contents-wrap .item:hover h3, .item.hover h3 { -webkit-transition-delay: 0.3s; transition-delay: 0.3s; }
.contents-wrap .item:hover h5, .contents-wrap .item.hover h5 { -webkit-transition-delay: 0.2s; transition-delay: 0.2s; }
/* 네번째 섹션 */
.Lince-wrap {
}
.table-design { display: table; padding: 20px 0; margin: 20% 0; width: 100%; margin: auto; }
.table-design:not(:last-child) { border-bottom: 1px solid #ddd; }
.table-design span { display: table-cell; }
.table-design h3 { display: table-cell; width: 100px; vertical-align: top; font-size: 2em; font-weight: bold; }
.table-design .item { height: auto; display: table-cell; vertical-align: middle; text-align: left; }
.table-design .item li span:nth-child(1) { width: 40px; font-size: 1em; font-weight: bold; color: #ffffff; }
/* 다섯번째 섹션 */
.contact-wrap { color: aliceblue;  height: auto;  width: 100%; margin: 0 auto;}
/* footer*/



.card {

  border: 1px solid #c2c2c2; background: white; border-radius: 3px; overflow: hidden; height: 350px; }
.card-top { width: 100%; /* height: 200px; */
  color: #000; }
.card-top h1 { font-size: 1rem; margin: 10px 20px; }
.card-top img { width: 100%; height: 200px; object-fit: cover; overflow: hidden; }
.card-bottom h3, .card-bottom p { margin: 10px 20px; color: #000; }
@media (max-width:786px) {
  .paddignFull { width: 100%; margin: 0 auto; margin-right: 16px; margin-left: 16px; }
  .intro-wrap .inner { flex-direction: column-reverse; }
  .intro-wrap .inner li{ margin: 20px 0;  }
  .about-wrap .skill-inner ul { display: grid; gap: 8px; grid-template-columns: repeat(2,1fr); margin-top: 20px; }
  .contents-wrap ul { display: grid; gap: 20px; grid-template-columns: repeat(2,1fr); margin-top: 20px; }
}