@charset "utf-8"; @import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,600;1,100;1,200&family=Noto+Sans+KR:wght@100;400&display=swap');:root * { font-family: 'Montserrat', sans-serif; font-family: 'Noto Sans KR', sans-serif; letter-spacing: 0px; box-sizing: border-box; }
* { margin: 0px; padding: 0px; box-sizing: border-box; color: #fff; }
footer { height: 50vh; padding: 70px 0 0 0; background-color: #000; width: 100%; margin: 0 auto; text-align: center; }
.grey { color: grey; }
footer .inner { padding: 50px 0; display: flex; justify-content: center; align-items: center; font-size: 16px; letter-spacing: -0.8px; gap: 50px; }
footer .inner li { display: flex; justify-content: center; flex-direction: column; align-items: center; }
footer .inner li:nth-child(1) i { background: url(../img/icon-mail.png); display: block; width: 32px; height: 32px; background-size: 100%; }
footer .inner li:nth-child(2) i { background: url(../img/icon-phone.png); display: block; width: 32px; height: 32px; background-size: 100%; }
footer .inner li:nth-child(3) i { background: url(../img/icon-map.png); display: block; width: 32px; height: 32px; background-size: 100%; }
footer div h6 { font-size: 26px; }
@media (max-width:786px){
    footer{height: auto;}
    footer .inner { display: flex; justify-content: center; flex-direction: column; align-items: center; }
    footer div h6 { font-size: 18px;}
}