@charset "utf-8"; @import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,600;1,100;1,200&family=Noto+Sans+KR:wght@100;400&display=swap');:root * { font-family: 'Montserrat', sans-serif; font-family: 'Noto Sans KR', sans-serif; letter-spacing: 0px; box-sizing: border-box;
}
header{ position: fixed;  background-color: #FFC86B; width: 100%; z-index: 10;  }
header .nav { width: 100%; display: flex; justify-content: center; align-items: center;}
header .nav li::after { content: ''; display: block; border-bottom: 5px solid #31333B; width: 0; transition: 0.8s; margin: auto; }
header .nav li a {  line-height: 70px; font-weight: bold; font-size: 1.2rem;  padding: 0 20px ; }
/*hover effect*/
header .nav li:hover a { color: #31333B; }
header .nav li:hover::after { width: 100%; }


/* 서브메뉴css */
.slider { 
  min-height: 100vh; display: flex; align-items: center; 
}
.slider ul{
  display: grid; gap: 10px; grid-template-columns: repeat(4,1fr); margin-top: 150px;
}
.slick-slide>div { margin: 0 10px; }
.slick-list { margin: 0 -10px; }



@media (max-width:786px){
  header .nav li a {  line-height: 50px; font-weight: bold; font-size: 15px; }

}