@charset "utf-8"; @import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,600;1,100;1,200&family=Noto+Sans+KR:wght@100;400&display=swap');:root * { font-family: 'Montserrat', sans-serif; font-family: 'Noto Sans KR', sans-serif; letter-spacing: 0px; box-sizing: border-box;
}
* { margin: 0px; padding: 0px; box-sizing: border-box; /* color: #000; */
}
.form-wrap {  min-width: 320px; max-width: 500px; margin: 0 auto; color: white;  margin-top: 50px; margin-bottom: 50px;}
.form-wrap form { /* background-color: #FFFFFF; */
  display: flex; align-items: center; justify-content: center; flex-direction: column; height: 100%; text-align: center; }
.form-wrap input { background-color: #eee; border: none; padding: 12px 15px; margin-bottom: 8px; width: 100%; color: black; border-radius: 8px; }
.form-wrap textarea { width: 100%; height: 200px; padding: 10px; color: black; border-radius: 8px; }
.form-wrap .btn { border-radius: 4px; height: 44px; font-size: 13px; font-weight: 600; text-transform: uppercase; -webkit-transition: all 200ms linear; transition: all 200ms linear; padding: 0 30px; letter-spacing: 1px; display: -webkit-inline-flex; display: -ms-inline-flexbox; display: inline-flex; -webkit-align-items: center; -moz-align-items: center; -ms-align-items: center; align-items: center; -webkit-justify-content: center; -moz-justify-content: center; -ms-justify-content: center; justify-content: center; -ms-flex-pack: center; text-align: center; border: none; background-color: #FFC86B; color: #000; /* box-shadow: 0 8px 24px 0 rgba(255,235,167,.2); */
}
.form-wrap .btn:active, .btn:focus { background-color: #000; color: #FFC86B;; /* box-shadow: 0 8px 24px 0 rgba(16,39,112,.2); */
}
.form-wrap .btn:hover { background-color: #000; color: #FFC86B; /* box-shadow: 0 8px 24px 0 rgba(16,39,112,.2); */
}
@media (max-width:786px) {
  .form-wrap { padding-right: 16px; padding-left: 16px;   margin-top: 50px; margin-bottom: 50px;}
}